.process {
    &__title {
        font-family: $font-title;
        font-size: 25px;
        line-height: 33px;
        color: $color-text;
        text-align: center;
        margin-bottom: 55px;

        @include media ('>=xl') {
            font-size: 43px;
            line-height: 57px;
            width: 685px;
            margin: 0 auto;
        }
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        // left: 50%;
        // transform: translateX(-50%);
        margin-top: 55px;
    }

    &__item {
        background: #FFFFFF;
        box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
        border-radius: 10px;
        width: 296px;
        margin: 0 auto;
        margin-bottom: 40px;

        @include media ('>=xl') {
            width: 260px;
        }
       
    }

    &__img{
        width: 296px;
        margin-bottom: 15px;
        height: 250px;
        @include media ('>=xl') {
            width: 260px;
            
        }
    }

    &__img img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 10px 10px 0px 0px;
    }

    &__item-title{
        font-family: $font-title;
        font-size: 20px;
        line-height: 26px;
        color: $color-text;
        padding-left: 20px;
        margin-bottom: 9px;
    }

    &__item-text{
        font-family: $font-text;
        font-size: 16px;
        line-height: 19px;
        color: $color-text;
        padding-left: 20px;
    }
}