
.site-footer{
    background: #F9F9F9;
}
.footer-inner{
    @include media ('>=md'){
        display: flex;
        justify-content: space-between;
    }
}
.logo-footer{
    margin-bottom: 28px;
}

.list__social{
    margin-top: 28px;
    display: flex;
    list-style-type: none;
    margin-left: -50px;
    justify-content: space-between;
}

.links-list{
    list-style-type: none;
    margin-left: -50px;
}
.links-item{
    margin-bottom: 15px;
}
.link{
    font-family: $font-text;
    font-size: 16px;
    line-height: 19px;
    color: $color-text;
}