.professional {
    background: #F9F9F9;
    padding-top: 29px;
    &__title {
        font-family: $font-title;
        font-size: 25px;
        line-height: 33px;
        color: $color-text;
        text-align: center;

        @include media ('>=xl'){
            font-size: 43px;
            line-height: 57px;
            width: 513px;
            margin: 0 auto;
        }
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        margin-top: 60px;
    }

    &__item {
        width: 315px;
        margin: 0 auto;
        margin-bottom: 40px;
    }

    &__img {
        width: 289px;
        height: 289px;
        margin-bottom: 20px;
    }

    &__img img {
        width: 100%;
        height: 100%;
        border: 2px solid $yellow;
        border-radius: 50%;
    }

    &__name {
        font-family: $font-header;
        font-size: 19px;
        line-height: 23px;
        color: $color-text;
        text-align: center;
        margin-bottom: 7px;
    }

    &__prof {
        font-family: $font-text;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: $color-text;
        margin-bottom: 7px;
    }
    
    &__text{
        font-family: $font-text;
        font-size: 16px;
        line-height: 19px;
        color: $color-text;
        text-align: center;
    }
}