.errors {
    padding-top: 50px;

    &__title {
        font-family: $font-title;
        font-size: 25px;
        line-height: 33px;
        color: $color-text;
        text-align: center;

        @include media ('>=xl'){
            font-size: 43px;
            line-height: 57px;
            width: 513px;
            margin: 0 auto;
            margin-bottom: 55px;
        }
    }

    &__item {
        background: #FFFFFF;
        box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
        border-radius: 10px;
        padding-bottom: 30px;
        
        width: 296px;
        margin: 0 auto;
        margin-bottom: 30px;
    }

    &__wrap {
        margin-top: 30px;
        display: flex;
        flex-wrap: wrap;
    }

    &__icon {

        width: 60px;
        height: 60px;

        position: relative;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 30px;
        margin-bottom: 10px;
    }

    &__item-title {
        font-family: $font-title;
        font-size: 22px;
        line-height: 29px;
        color: $color-text;
        text-align: center;
        width: 243px;
        margin: 0 auto;
    }

    &__item-text {
        font-family: $font-text;
        font-size: 16px;
        line-height: 19px;
        color: $color-text;
        text-align: center;
        padding: 0 5px;
    }
}